import React from 'react';
import ReactDOM from 'react-dom';
import * as func from './function'
import * as display from './display'
class Apl extends React.Component {
    constructor(props){
        super(props);
        this.state ={
            row : 0,
            column : 0,
            use : '',
            result : '',
            count : 0,
            display: false
        };
        this.setMatrixRow= this.setMatrixRow.bind(this);
        this.setMatrixColumn = this.setMatrixColumn.bind(this);
        this.setUse = this.setUse.bind(this);
        this.infoFromMatrix = this.infoFromMatrix.bind(this);
        this.infoFromMatrix1 = this.infoFromMatrix1.bind(this);
        this.setCount = this.setCount.bind(this);
        this.setMatrixRowAndColumn = this.setMatrixRowAndColumn.bind(this);
    }
    infoFromMatrix() {
        let zhach = Array(Number(this.state.row));
        for(let i = 0; i <  this.state.row; i++)
        {
            zhach[i]= new Array(Number(this.state.column))
        }
        let count = 0,element;
        for(let i = 0; i <  this.state.row; i++)
            for(let j = 0; j <  this.state.column; j++){
                element = document.getElementById(String(count));
                 zhach[i][j] = element.value;
                count = count + 1;
            }
        let result;
            switch (this.state.use) {
                case '':
                    result = func.TransMatrix(zhach);
                    break;
                case '':
                    result = func.Determinant(zhach);
                    break;
                case '':
                    result = func.MatrixRank(zhach);
                    break;
                case '':
                    result = func.AdjugateMatrix(zhach);
                    break;
                case '':
                    result = func.InverseMatrix(zhach);
                    break;
                default : break;
            }
            console.log(result);
            this.setState({
                result: result,
                display : true
            });
        return zhach
    }
    infoFromMatrix1() {
        let firstZhach = this.infoFromMatrix();
        let zhach = Array(Number(this.state.row));
        for(let i = 0; i <  this.state.row; i++)
        {
            zhach[i]= new Array(Number(this.state.column))
        }
        let  element;
        let count = display.count;
        for(let i = 0; i <  this.state.row; i++)
            for(let j = 0; j <  this.state.column; j++){
                element = document.getElementById(String(count));
                zhach[i][j] = element.value;
                count = count + 1;
            }
        console.log(firstZhach);
        let result;
        switch (this.state.use){
            case '': result = func.SumMatrix(firstZhach,zhach); break;
            case '': result = func.MultiplyMatrix(firstZhach,zhach); break;
            case '': result = func.SubMatrix(firstZhach,zhach); break;
            default: break;
        }
        console.log(result);
        this.setState({
            result : result,
            display : true
        })
    }
    setMatrixRowAndColumn(e){
        let a = e.target.value;
        this.setState({
            row : a,
            column : a
        });
    }
    setMatrixRow(e){
        let a = e.target.value;
        this.setState({
            row : a
        });
    }
    setCount(e){
        let a = e.target.value;
        this.setState({
            count : a,
            display : false
        });
    }
    setMatrixColumn(e){
        let a = e.target.value;
        this.setState({
            column : a
        });
    }
    setUse(e){
        let a = e.target.value;
        this.setState({
            use : a,
            display : false
        });
    }
    render(){

        if(this.state.count === 0)
        {return(
            <div className='main'>
                <div className='dop'><display.DopInfo /></div>
                <div>
                <h1>One or two matrix?</h1>
                <div className='chooseMatrix' onClick={this.setCount}>
                    <button value= '1'> 1 </button>
                    <button value= '2'> 2 </button>
                </div>
                </div>
                <div className='dop'><display.DopInfo2 /></div>
            </div>
        )
        };
        if(+this.state.count === 1)
            return(
                <div className='main'>
                <div className='dop'><display.DopInfo /></div>
                <div>
                <h1>One or two matrix?</h1>
                <div>
                    <div className='chooseMatrix' onClick={this.setCount}>
                        <button value= '1'> 1 </button>
                        <button value= '2'> 2 </button>
                    </div>
                </div>

                <br/>
                    <h2>   <span className='red'>*</span></h2>
                    <select onClick={this.setUse}>
                        <option></option>
                        <option></option>
                        <option></option>
                        <option></option>
                        <option></option>
                    </select>
                    <br/>
                    {this.state.use ? <display.Choise state={this.state} change={this.setMatrixRowAndColumn} row={this.setMatrixRow} column={this.setMatrixColumn}/> : <div></div>}

                    <br/>
                <display.DrawMatrix state={this.state}/>
                    <br/>
                {(this.state.use) ? <input type='submit' onClick={this.infoFromMatrix} value=""/> : false}
                <br/>
                    <br/>
                    <br/>
                    {this.state.display ? <display.DrawResult state={this.state}/> : <div></div>}
                </div>
                    <div className='dop'><display.DopInfo2 /></div>
            </div>
        )
        if(+this.state.count === 2)
            return(
                <div className='main'>
                    <div className='dop'><display.DopInfo /></div>
                    <div>
                    <h1>One or two matrix?</h1>
                    <div className='chooseMatrix' onClick={this.setCount}>
                        <button value= '1'> 1 </button>
                        <button value= '2'> 2 </button>
                    </div>
                    <h2>  <span className='red'>*</span></h2>
                    <display.SelectSize work={this.setMatrixRow}/>
                    <h2>  <span className='red'>*</span></h2>
                    <display.SelectSize work={this.setMatrixColumn}/>
                    <br/>
                    <h2>   <span className='red'>*</span></h2>
                    <select onClick={this.setUse}>
                        <option></option>
                        <option></option>
                        <option></option>
                    </select>
                    <br/>
                    <display.DrawMatrix state={this.state}/>
                    <br/>
                    <display.DrawMatrix1 state={this.state}/>
                    <br/>
                    {(this.state.use === '' ||this.state.use === '' || this.state.use === '') ? <input type='submit' onClick={this.infoFromMatrix1} value=""/> : false}
                    <br/>
                    <br/>
                    <br/>
                    {this.state.display ? <display.DrawResult state={this.state}/> : <div></div>}
                    </div>
                    <div className='dop'><display.DopInfo2 /></div>
                </div>
            );

        else return <div>wtf?</div>

    }
}

ReactDOM.render(<Apl />, document.getElementById('root'));




